import Index from '@/views/Index.vue'
import Home from '@/views/home/Home.vue'
import Hot from '@/views/hot/Hot.vue'
import Newest from '@/views/newest/Newest.vue'
import Classification from '@/views/classification/Classification.vue'
import Statistics from '@/views/statistics/Statistics.vue'
const routes = [
  {
    path: '/',
    redirect: '/home',
    component: Index,
    children: [
      {
        path: 'home',
        meta: {
          title: '首页'
        },
        component: Home
      },
      {
        path: 'hot',
        meta: {
          title: '热门舆情'
        },
        component: Hot,
      },
      {
        path: 'newest',
        meta: {
          title: '最新舆情'
        },
        component: Newest,
      },
      {
        path: 'classification',
        meta: {
          title: '舆情分类'
        },
        component: Classification,
      },
      {
        path: 'statistics',
        meta: {
          title: '舆情统计'
        },
        component: Statistics
      }
    ]
  },
  // {
  //   path: '*',
  //   redirect: '/404',
  // }
]
export default routes



